.aboutContent {

    grid-row: 2/3;
    grid-column: 2/3;

    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;

    width: 100%;

    &__groupCollapse{
        display: flex;
        flex-direction: column;

        gap: 20px;

        margin: 0 auto;

        width:100%;
    }
}